import Cookies from 'js-cookie'

export default {
  name: "Promotion",
  data: function () {
    return {
      queryRewardTraceByLoginIdRequest: 'promotion-service/1.0.0/rotary_table_activity/queryRewardTraceByLoginId',
      rotatingFlag: false,
      accessToken: '',
      emptyPrizeFlag: true,
      prizeData: [],
      loading: false
    }
  },
  computed: {
    userActivityId() {
      return this.$route.query.state;
    },
    identityCode() {
      return this.$route.query.state;

    },
    wechatAuthCode() {
      return this.$route.query.code
    },
    stateCode() {
      return this.$route.query.state
    },
    token() {
      return this.$route.params.token;
    }
    // canvasWidth() {
    //   return this.remUnit * 13.5 + 'px';
    // },
    // canvasHeight() {
    //   return this.remUnit * 13.5 + 'px';
    // },
  },
  watch: {
    token() {
      this.accessToken = this.token;

    }
  },
  beforeMount() {

  },
  mounted() {
    this.$remResizing({
      fontSize: 20,
    });
    console.log(this.$route.query.token)
    this.getMyPrizeData();
  },
  methods: {
    getMyPrizeData() {
      this.loading = true;
      this.$http.get(this.$baseUrl + this.queryRewardTraceByLoginIdRequest, {
        params: {
          loginId: this.$route.query.loginId,
        },
        headers: {
          'Authorization': 'Bearer ' + Cookies.get('wheel-accessToken')
        }
      }).then(response => {
        console.log(response)
        this.loading = false;
        response = response.data;
        this.emptyPrizeFlag = response === null
        this.prizeData = response;
      }).catch(error => {
        this.loading = false;
        this.$vux.confirm.show({
          showCancelButton: false,
          title: error.message,
          onConfirm() {
          }
        })
      })
    }
  }
}





